<template>
  <div class="container">
    <div class="container-content">
      <div class="container-header">
        <div class="header-title">
          <div class="header-left">{{ $t('cardMag.basicInformation') }}</div>
          <!--          <div>-->
          <!--            <el-button v-compPriv="88" type="primary" @click="changePackage">套餐变更</el-button>-->
          <!--            <el-button v-compPriv="87" style="margin: 0 12px" type="primary" @click="changeAttr">属性变更</el-button>-->
          <!--            <el-button v-compPriv="89" type="primary" :loading="loading" @click="refreshStatus">状态刷新</el-button>-->
          <!--            <el-button v-compPriv="111" style="margin: 0 12px" type="primary" @click="changeOrder">套餐订购</el-button>-->
          <!--          </div>-->
        </div>
        <div class="header-info">
          <el-row :gutter="20">
            <el-col v-for="obj in infoList" :key="obj.prop" :span="6">
              <CtTooltip :content="itemInfoFormatter(obj)"></CtTooltip>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="container-middle">
        <div class="middle-title">
          <div class="middle-left">{{ $t('cardMag.superCardDetail') }}</div>
        </div>
        <FullDetailList :tableData="tableData"></FullDetailList>
      </div>
      <div class="container-bottom">
        <div class="seperator-line"></div>
        <el-tabs v-model="activeTab" class="page-tabs page-cont">
          <el-tab-pane :label="$t('cardMag.packageInformation')" name="1">
            <PackageInfo
              ref="packageInfoRef"
              :full-msisdn="fullMsisdn"
              :block-status="detailInfo.blockStatus"
              @changedService="qrySimDetail"
            ></PackageInfo>
          </el-tab-pane>
          <el-tab-pane :label="$t('cardMag.APNstatus')" name="2">
            <ApnStatus ref="apnStatusRef" :fullMsisdn="fullMsisdn"></ApnStatus>
          </el-tab-pane>
          <el-tab-pane :label="$t('cardMag.changeLog')" name="3">
            <History ref="historyRef" :fullMsisdn="fullMsisdn"> </History>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
    <ChangePackageDialog
      v-if="changePackageDialogVisible"
      :visible.sync="changePackageDialogVisible"
      :detail-info="detailInfo"
      @refresh="refresh"
    ></ChangePackageDialog>
    <ChangAttrDialog
      v-if="changeAttrDialogVisible"
      :visible.sync="changeAttrDialogVisible"
      :detail-info="detailInfo"
    ></ChangAttrDialog>
    <ChangOrderDialog
      v-if="changeOrderDialogVisible"
      :visible.sync="changeOrderDialogVisible"
      :detail-info="detailInfo"
      @refresh="refresh"
    ></ChangOrderDialog>
  </div>
</template>
<script>
import PackageInfo from './packageInfo'
import ApnStatus from './apnStatus'
import History from './history'
import FullDetailList from './fullDetailList'
import ChangePackageDialog from './dialogs/ChangePackageDialog'
import ChangAttrDialog from './dialogs/ChangAttrDialog'
import ChangOrderDialog from './dialogs/ChangOrderDialog.vue'
export default {
  components: {
    PackageInfo,
    ApnStatus,
    History,
    FullDetailList,
    ChangAttrDialog,
    ChangePackageDialog,
    ChangOrderDialog,
  },
  data() {
    return {
      fullMsisdn: this.$route.query.fullMsisdn,
      imeiStatus: this.$route.query.imeiStatus,
      netIspId: this.$route.query.netIspId,
      infoList: [
        {
          label: this.$t('cardMag.fullMsisdn'),
          prop: 'fullMsisdn',
        },
        {
          label: this.$t('cardMag.equipmentSupplierId'),
          prop: 'equipmentSupplierName',
        },
        // {
        //   label: this.$t('cardMag.fullCardSource'),
        //   prop: 'fullCardSource',
        //   formatter: (val) => this.fullCardSourceDesc(val),
        // },
        {
          label: this.$t('cardMag.custId'),
          prop: 'custId',
          formatter: (val) => this.custName(val),
        },
        {
          label: this.$t('cardMag.channelId'),
          prop: 'channelName',
        },
        {
          label: this.$t('cardMag.refreshTime'),
          prop: 'refreshTime',
        },
        {
          label: this.$t('cardMag.importTaskId'),
          prop: 'importTaskId',
        },
        {
          label: this.$t('cardMag.netIspName'),
          prop: 'netIspId',
          formatter: (val) => this.custName(val),
        },
        {
          label: 'IMEI',
          prop: 'imei',
        },
        {
          label: this.$t('cardMag.imeiStatusDesc'),
          prop: 'imeiStatus',
          formatter: (val) => this.imeiStatusDesc(val),
        },
        {
          label: this.$t('cardMag.initSimStatus'),
          prop: 'initSimNum',
          formatter: (val) => this.simStatusDesc(val),
        },
        {
          label: this.$t('cardMag.initValidPeriod'),
          prop: 'initValidPeriod',
          formatter: (val) => this.dayFormat(val),
        },
        {
          label: this.$t('cardMag.stepSimStatus'),
          prop: 'stepSimStatus',
        },
        {
          label: this.$t('cardMag.testOfferName'),
          prop: 'testOfferName',
        },
        {
          label: this.$t('cardMag.testOfferOverAction'),
          prop: 'testOfferOverAction',
        },
        {
          label: this.$t('cardMag.mainOfferNameDetail'),
          prop: 'mainOfferName',
        },
        {
          label: this.$t('cardMag.simStatusDesc'),
          prop: 'simStatus',
          formatter: (val) => this.simStatusDesc(val),
        },
        {
          label: this.$t('cardMag.activeTime'),
          prop: 'activeTime',
        },
        {
          label: this.$t('cardMag.ispComposeType'),
          prop: 'ispComposeType',
          formatter: (val) => this.ispComposeTypeDesc(val),
        },
        {
          label: this.$t('cardMag.ispComposeValueDesc'),
          prop: 'ispComposeValue',
          formatter: (val) => this.ispComposeValueDesc(val),
        },
        {
          label: this.$t('cardMag.sdk'),
          prop: 'sdk',
          formatter: (val) => this.sdkDesc(val),
        },
        {
          label: this.$t('cardMag.strategyName'),
          prop: 'strategyName',
        },
        {
          label: this.$t('cardMag.fullServMsisdn'),
          prop: 'fullServMsisdn',
        },
      ],
      info: {},
      activeTab: '1',
      changePackageDialogVisible: false,
      changeAttrDialogVisible: false,
      changeOrderDialogVisible: false,
      loading: false,
      detailInfo: {},
      tableData: [],
    }
  },
  computed: {
    custName() {
      return (custId) => {
        if (custId == 1) {
          return this.$t('cardMag.cm')
        }
        if (custId == 2) {
          return this.$t('cardMag.cu')
        }
        if (custId == 3) {
          return this.$t('cardMag.ct')
        }
        return '-'
      }
    },
    fullCardSourceDesc() {
      return (fullCardSource) => {
        if (fullCardSource == '1000') {
          return this.$i18n.locale === 'zh' ? '标准卡' : 'Norm card'
        }
        if (fullCardSource == '2000') {
          return this.$i18n.locale === 'zh' ? '定制卡' : 'Custom card'
        }
        return '-'
      }
    },
    ispComposeTypeDesc() {
      return (ispComposeType) => {
        if (ispComposeType == '2000') {
          return this.$i18n.locale === 'zh' ? '双网' : 'Dual networks'
        }
        if (ispComposeType == '3000') {
          return this.$i18n.locale === 'zh' ? '三网' : 'Three networks'
        }
        return '-'
      }
    },
    sdkDesc() {
      return (sdk) => {
        if (sdk == '1') {
          return this.$i18n.locale === 'zh' ? 'SDK切换' : 'SDK switching'
        }
        if (sdk == '2') {
          return this.$i18n.locale === 'zh' ? '重启切换' : 'Restart switching'
        }
        if (sdk == '3') {
          return this.$i18n.locale === 'zh' ? '断网切换' : 'Off-network switching'
        }
        return '-'
      }
    },
    imeiStatusDesc() {
      return (imeiStatus) => {
        if (imeiStatus == '0') {
          return this.$i18n.locale === 'zh' ? '离线' : 'Offline'
        }
        if (imeiStatus == '1') {
          return this.$i18n.locale === 'zh' ? '在线' : 'Online'
        }
        return '-'
      }
    },
    simStatusDesc() {
      return (simStatus) => {
        if (simStatus == '100000') {
          return this.$i18n.locale === 'zh' ? '在用' : 'Active'
        }
        if (simStatus == '130000') {
          return this.$i18n.locale === 'zh' ? '测试激活' : 'Test activation'
        }
        if (simStatus == '140002') {
          return this.$i18n.locale === 'zh' ? '可激活' : 'Can be activated'
        }
        return '-'
      }
    },
    ispComposeValueDesc() {
      return (ispComposeValue) => {
        if (ispComposeValue == '12') {
          return this.$i18n.locale === 'zh' ? '电信+联通' : 'Telecom + Unicom'
        }
        if (ispComposeValue == '13') {
          return this.$i18n.locale === 'zh' ? '电信+移动' : 'Telecom + Mobile'
        }
        if (ispComposeValue == '123') {
          return this.$i18n.locale === 'zh'
            ? '电信+联通+移动'
            : 'Telecom + Unicom + Mobile'
        }
        return '-'
      }
    },
  },
  created() {
    this.qrySimDetail()
  },
  methods: {
    queryJTCardCurrentInfo(netIspId) {
      if (this.imeiStatus == '1') {
        this.tableData.forEach((item) => {
          item.cardOnlineNum = '0'
          item.cardOnlineStatus = '离线'
        })
      }
      if (netIspId == 1) {
        // 移动
        this.detailInfo.netIspId = 1
        this.detailInfo.netIspName = '中国移动'
        this.tableData[1].cardOnlineNum = '1'
        this.tableData[1].cardOnlineStatus = '在线'
      }
      if (netIspId == 2) {
        // 联通
        this.detailInfo.netIspId = 2
        this.detailInfo.netIspName = '中国联通'
        this.tableData[0].cardOnlineNum = '1'
        this.tableData[0].cardOnlineStatus = '在线'
      }
      if (netIspId == 3) {
        // 电信
        this.detailInfo.netIspId = 3
        this.detailInfo.netIspName = '中国电信'
        this.tableData[2].cardOnlineNum = '1'
        this.tableData[2].cardOnlineStatus = '在线'
      }
    },
    async qrySimDetail() {
      const param = { fullMsisdn: this.fullMsisdn }
      this.$api.fullCardMag.qryFullSimDetail(param).then((res) => {
        this.detailInfo = res
        this.tableData = this.detailInfo?.fullProdInstList
        this.queryJTCardCurrentInfo(this.netIspId * 1)
      })
    },
    itemInfoFormatter(obj) {
      const des = obj.formatter
        ? obj.formatter(this.detailInfo[obj.prop])
        : this.detailInfo[obj.prop] ?? '-'
      return `${obj.label}：${des}`
      // return obj.label + ':' + this.detailInfo[obj.prop] || ''
    },
    refreshStatus() {
      this.loading = true
      this.$api.fullCardMag
        .updateFullSimData({ fullMsisdn: this.fullMsisdn })
        .then((res) => {
          this.$message.success(`状态刷新成功！`)
          this.qrySimDetail()
        })
        .finally(() => {
          this.loading = false
        })
    },
    changePackage() {
      this.changePackageDialogVisible = true
    },
    changeAttr() {
      this.changeAttrDialogVisible = true
    },
    changeOrder() {
      this.changeOrderDialogVisible = true
    },
    refresh() {
      this.qrySimDetail()
      this.$refs.packageInfoRef.queryFullOfferInsts()
      this.$refs.apnStatusRef.qryFullSimCardOnlineInfo()
      this.$refs.historyRef.qryFullSimChangeHis()
    },
    dayFormat(days) {
      return days > 0 ? `${days}天` : `-`
    },
  },
}
</script>
