<template>
  <div class="summary">
    <el-table
      ref="table"
      :element-loading-text="$t('cardMag.loading')"
      :data="tableData"
      :border="true"
    >
      <el-table-column
        prop="ispName"
        :label="$t('cardMag.ispName')"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          {{custName(scope.row.ispId)}}
        </template>
      </el-table-column>
      <el-table-column
        prop="supplierName"
        :label="$t('cardMag.supplierName')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column prop="iccid" label="ICCID" show-overflow-tooltip></el-table-column>
      <el-table-column
        prop="msisdn"
        label="MSISDN"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column prop="imsi" label="IMSI" show-overflow-tooltip></el-table-column>
      <el-table-column
        prop="cardOnlineStatus"
        :label="$t('cardMag.cardOnlineStatus')"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          {{imeiStatusDesc(scope.row.cardOnlineNum)}}
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    tableData: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    custName() {
      return (custId)=>{
        if(custId == 3){
          return this.$t('cardMag.cm')
        }
        if(custId == 2){
          return this.$t('cardMag.cu')
        }
        if(custId == 1){
          return this.$t('cardMag.ct')
        }
        return '-'
      }
    },
    imeiStatusDesc(){
      return (imeiStatus)=>{
        if(imeiStatus == '0'){
          return this.$i18n.locale === 'zh'?'离线':'Offline'
        }
        if(imeiStatus == '1'){
          return this.$i18n.locale === 'zh'?'在线':'Online'
        }
        return '-'
      }
    },
  }
}
</script>
<style lang="scss" scoped>
.summary {
  // display: flex;
  padding: 0 24px 24px;
}
</style>
